
import { defineComponent, ref, onMounted } from "vue"
import useYandex from "./hooks/yandex"
import { refType, YandexType } from "@/types/yandexType"

export default defineComponent({
	setup() {
		const yamaps = ref<refType>(null)

		const init = async () => {
			const options: YandexType = {
				el: yamaps.value,
				coordinate: [66.08869000789291, 76.67697540222169],
				zoom: 13,
				markerCoordinate: [66.08869000789291, 76.67697540222169],
			}

			const { initYandexMap } = useYandex(options)

			await initYandexMap()
		}

		onMounted(init)

		return { yamaps }
	},
})
